.Entete{
    height: 50px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    font-size: 1.2rem;
    color: white;
    background-color: rgb(49, 48, 48);
}
.Entete p:first-child{
    margin-left: 20px;
}
.Entete p:last-child{
    margin-right: 40px;
}
.Avatar{
    position: absolute;
    top: 12px;
}
